<template>
  <div class="container mt-5">
    <h1 class="text-center mb-4">After School Activities</h1>
    
    <!-- Sort section -->
    <div class="d-flex justify-content-end mb-3">
      <label for="sort" class="mr-2">Sort By:</label>
      <select v-model="sortAttribute" class="form-control w-auto mr-2" @change="sortLessons">
        <option value="subject">Subject</option>
        <option value="location">Location</option>
        <option value="price">Price</option>
        <option value="spaces">Spaces</option>
      </select>
      <button class="btn btn-primary" @click="toggleSortOrder">
        {{ sortOrder === 'asc' ? 'Ascending' : 'Descending' }}
      </button>
    </div>
    
    <!-- Cart Section -->
    <div class="mb-4">
      <h3>Cart ({{ cart.length }} items)</h3>
      <ul>
        <li v-for="item in cart" :key="item.id">{{ item.subject }} - ${{ item.price }}</li>
      </ul>
      <button v-if="cart.length > 0" class="btn btn-success">Checkout</button>
    </div>
    
    <!-- Lesson Cards -->
    <div class="row">
      <LessonCard
        v-for="lesson in lessons"
        :key="lesson.id"
        :lesson="lesson"
        @add-to-cart="addToCart"
      />
    </div>
  </div>
</template>

<script>
import LessonCard from './components/LessonCard.vue';

export default {
  name: 'App',
  components: {
    LessonCard,
  },
  data() {
    return {
      lessons: [
        { id: 1, subject: 'Math', location: 'Room A', price: 10, spaces: 5, icon: 'fas fa-calculator' },
        { id: 2, subject: 'Science', location: 'Room B', price: 15, spaces: 3, icon: 'fas fa-flask' },
        { id: 3, subject: 'English', location: 'Room C', price: 12, spaces: 4, icon: 'fas fa-book' },
        { id: 4, subject: 'Swimming', location: 'Pool', price: 20, spaces: 6, icon: 'fas fa-swimmer' },
        { id: 5, subject: 'French', location: 'Room D', price: 18, spaces: 7, icon: 'fas fa-language' },
        // Add more lessons as needed
      ],
      sortAttribute: 'subject',
      sortOrder: 'asc',
      cart: []  // Array to store the lessons added to the cart
    };
  },
  methods: {
    sortLessons() {
      const order = this.sortOrder === 'asc' ? 1 : -1;
      this.lessons.sort((a, b) =>
        a[this.sortAttribute] > b[this.sortAttribute] ? order : -order
      );
    },
    toggleSortOrder() {
      this.sortOrder = this.sortOrder === 'asc' ? 'desc' : 'asc';
      this.sortLessons();
    },
    addToCart(lesson) {
      // Add the lesson to the cart
      this.cart.push(lesson);
    },
  },
};
</script>

<style scoped>
h1 {
  color: #4CAF50;
}
</style>
